import { Link } from 'react-scroll';

import styles from './projects.module.scss'
import H2 from '../../../shared/UI/h2/h2'
import Button from '../../../shared/UI/button/button'

import lawyers from '../../../shared/images/lawyers.png'
import psy from '../../../shared/images/psy.png'
import moder from '../../../shared/images/moder.webp'

const Projects = () => {
  return (
    <section className={styles.projects} id='projects'>
      <H2 className={styles.title}> </H2>
      <ul className={styles.list}>
        <li className={styles.item}>
          <img className={styles.itemImage} src={lawyers} alt="  " />
          <p className={styles.itemSubtitle}>  </p>
        </li>
        <li className={styles.item}>
          <img className={styles.itemImage} src={psy} alt=" " />
          <p className={styles.itemSubtitle}> </p>
        </li>
        <li className={styles.item}>
          <img className={styles.itemImage} src={moder} alt="RM + CMS" />
          <p className={styles.itemSubtitle}>RM + CMS</p>
        </li>
      </ul>
      <div className={styles.footer}>
        <Link to='form' smooth={true} duration={1000}>
          <Button> </Button>
        </Link>
        <span className={styles.comment}> !</span>
      </div>

    </section>
  )
};

export default Projects;
